import Mock from 'mockjs';

const html = `
<table class="cd-table">
<thead>
  <tr>
    <th class="table-sort checked" data-target="#my-table" data-toggle="cd-table-sort" data-sort-key="id">
      
      <div class="table-sort-icon">
        <i class="cd-icon cd-icon-arrow-line-up active" data-sort-value="asc"></i>
        <i class="cd-icon cd-icon-arrow-line-down" data-sort-value="desc"></i>
      </div>
    </th>
    <th></th>
    <th class="table-sort"></th>
    <th></th>
    <th>
      <div class="dropdown">
         
        <a href="javascrip:;" class="cd-link-gray-primary" data-toggle="dropdown">
          <i class="cd-icon cd-icon-filter cd-text-xs"></i>
        </a>
        <ul class="dropdown-menu cd-dropdown-menu cd-dropdown-menu-filter">
          <li class="active"><a href="javascript:;" data-toggle="cd-table-filter" data-filter-key="status"></a></li>
          <li><a href="javascript:;" data-toggle="cd-table-filter" data-filter-key="status" data-filter-value="1"></a></li>
          <li><a href="javascript:;" data-toggle="cd-table-filter" data-filter-key="status" data-filter-value="2"></a></li>
        </ul>
      </div>
    </th>
    <th></th>
    <th></th>
  </tr>
</thead>
<tbody>
  <tr>
    <td>1</td>
    <td> 200 </td>
    <td>2017042121144446163</td>
    <td>V2017042121141876634</td>
    <td><span class="cd-status cd-status-info"></span></td>
    <td>+200.00</td>
    <td><a href="javascript:;" class="cd-link-primary"></a></td>
  </tr>
  <tr>
    <td>2</td>
    <td> 100 </td>
    <td>2017042121144446164</td>
    <td>V2017042121141876635</td>
    <td><span class="cd-status cd-status-warning"></span></td>
    <td>+100.00</td>
    <td><a href="javascript:;" class="cd-link-primary"></a></td>
  </tr>
  <tr>
    <td>3</td>
    <td> 50 </td>
    <td>2017042121144446166</td>
    <td>V2017042121141876637</td>
    <td><span class="cd-status cd-status-disabled"></span></td>
    <td>+50.00</td>
    <td><a href="javascript:;" class="cd-link-primary"></a></td>
  </tr>
</tbody>
</table>
`;

Mock.mock('/demo/table', html);

Mock.mock(/\/demo\/tabs\/[0-9]+/, {
  'message': 'Ajax ',
  'id|1-100': 100 
});



